.contact{
    padding-top:5rem;  
    padding-bottom: 2rem;
    padding-left: 4rem;
    padding-right: 3rem;
    overflow: hidden;
}
.contact-heading{
    text-align: center;
    padding-bottom: 50px;
}
.contact-heading h2{
    padding-bottom: 20px;
    font-weight: 900;
    font-size: 35px;
    font-family: 'Nuosu SIL', serif;
    text-transform: uppercase;
    color: #3c3c3c;
}
.contact-heading p{
    font-size: 15px;
    font-weight: 400;
    width: 50%;
    margin: auto;
    font-family: 'Poppins', sans-serif;
    padding-bottom: 20px;
}
.contact-row{
    padding-top: 2rem;
    display: flex;
    justify-content: space-between;
}
.contact-column{
    width: 45%;
}
.contact-widget{
    margin-bottom: 40px;
    padding-right: 10px;
}
.contact-widget-item{
    overflow: hidden;
    margin-bottom: 40px;
}
.icon{
    height: 60px;
    width: 60px;
    background: #5540BF;
    border-radius: 50%;
    line-height: 60px;
    text-align: center;
    margin-right: 30px;
    float: left;
}
.icon i{
    font-size: 24px;
    color: #fff;
    position: relative;
    top: 3px;
}
.contact-text{
    overflow: hidden;
}
.contact-text h5{
    font-size: 20px;
    font-weight: 500;
    margin-bottom: 6px;
}
.contact-text p{
    font-size: 16px;
    font-family: "Bai Jamjuree",sans-serif;
    font-weight: 400;
    line-height: 28px;
    margin: 0 0 15px 0;
}
.contact-form{
    text-align: center;
    margin-bottom: 40px;
}
form{
    display: block;
    margin-top: 0;
}
form input{
    height: 46px;
    width: 100%;
    padding-left: 20px;
    font-size: 15px;
    color: #1b191b;
    border: 1px solid #e1e1e1;
    margin-bottom: 20px;
    border-radius: 4px;
    transition: all , .3s;
}
form textarea{
    height: 110px;
    width: 100%;
    padding-left: 20px;
    padding-top: 12px;
    font-size: 15px;
    color: #1b191b;
    border: 1px solid #e1e1e1;
    resize: none;
    margin-bottom: 9px;
    border-radius: 4px;
    transition: all , .3s;
}
form button{
    width: 100%;
    cursor: pointer;
}
.site-btn{
    border: none;
    display: inline-block;
    background: #5540BF;
    color: white;
    font-size: 14px;
    font-family: "Rubik",sans-serif;
    font-weight: 500;
    letter-spacing: 2px;
    text-transform: uppercase;
    border-radius: 50px;
    padding: 12px 40px 10px;
}
@media (max-width: 768px){
  .contact{
    padding: 4rem 2rem 1rem 2rem;
    height: 100%;
  }
  .contact-heading p{
    width: 100%;
}
    .contact-row{
    display: block;
  }
  .contact-column{
    width: 100%;
   }
}
@media (max-width: 500px){
    .contact-heading{
        padding-right: 0;
    }
    .contact-heading h2{
        font-size: 22px; 
    }
    .contact-heading h2::before{
        right: -50px;
        width: 40px;
    }
    .contact-heading h2::after{
        left: -50px;
        width: 40px;
    } 
}